.App {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
}
h1,
h2,
h3,
h4,
p,
span,
button,
ul {
  margin: 0;
  padding: 0;
}
/* home */
.homeTable-container {
  width: 80%;
  margin: 0 auto;
  padding-top: 40px;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

/* Droppable  */

.dnd-contianer {
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
}

.dnd-ul {
  margin-top: 20px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 40px;
}
.dnd-ul li {
  list-style: none;
  font-weight: 700;
}

.drop-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.drop-item.active {
  border: 1px solid red;
}
.drop-item:hover {
  border-bottom: 1px solid black;
}
.drop-item li {
  width: 150px;
  height: 40px;
  padding: 20px 0px;
  list-style: none;
  display: flex;
  align-items: center;
}

.button-control {
  width: 200px;
  display: flex;
  gap: 10px;
}
.button-control button {
  width: 150px;
}
/* HOme header */

.header-container {
  width: 100%;
  display: flex;
  height: 30px;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid black;
}
.header-container ul li {
  text-align: center;
  list-style: none;
  font-weight: 700;
  width: 150px;
}

/* Table list  */
.TableList-container {
  width: 100%;
}

.TableList-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  background-color: brown;
  height: 150px;
  padding: 0 10px;
  border-radius: 10px;
  box-shadow: 3px 10px 10px rgba(0, 0, 0, 0.2);
  color: rgb(187, 178, 178);
}

.TableList-header ul {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.TableList-header ul li {
  display: flex;
  flex-direction: column;
  gap: 5px;
  font-size: 18px;
  font-weight: 700;
}
.TableList-header ul li input,
select {
  border-radius: 4px;
  width: 100%;
  height: 30px;
  padding: 0 3px;
}

.TableList-header ul li.last-child {
  flex-direction: row;
  align-items: end;
  gap: 30px;
}
.TableList-header ul li.last-child button {
  width: 150px;
  height: 30px;
  border-radius: 10px;
  font-weight: 700;
}

.switch-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}
.switch-case {
  width: 80px;
  padding-left: 2px;
  display: flex;
  border: 1px solid red;
  border-radius: 10px;
  align-items: center;
  justify-content: space-between;
}

.switch-case span {
  width: 40px;
  height: 25px;
  border: 1px solid red;
  border-radius: 20px;
  background-color: red;
}

.switch-case #on {
  width: 0px;
  overflow: hidden;
  visibility: hidden;
}
.switch-case #off {
  color: red;
}
/* Table List  */
.TableListData-container {
  width: 100%;
}
.TableListData-container h1 {
  text-align: center;
  padding: 20px 0;
}

.data-header {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}
.data-header li {
  width: 300px;
  list-style: none;
  text-align: center;
  font-weight: 700;
}

.data-inputs ul {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}
.data-inputs ul li {
  width: 300px;
  list-style: none;
  text-align: center;
  height: 30px;
  margin-top: 30px;
  color: rgba(0, 0, 0, 0.7);
}

.button-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30px;
  width: 100%;
  margin: 30px 0;
  gap: 30px;
}
.button-container button {
  height: 30px;
  width: 150px;
  border-radius: 5px;
  font-weight: 700;
}

/* Sort container */

.sort-container {
  width: 100%;
  height: 40px;
  cursor: pointer;
}
.sort-container ul {
  display: flex;
  height: 30px;
}

.error-container {
  width: 100%;
  height: 40px;
  background-color: rgba(0, 0, 0, 0.3);
  position: absolute;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.error-container p {
  width: 100%;
  text-align: center;
  font-size: 18px;
  word-spacing: 5px;
  font-weight: 700;
  color: rgb(240, 31, 31);
}

.error-edit {
  display: flex;
  width: 100%;

  margin-top: 30px;
}

.error-edit p {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  font-weight: 700;
  color: RED;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  height: 40px;
}
